<template>
  <div class="umis-editor-container">
    <div ref="editor" class="monaco-editor" />
    <div class="umis-editor-tools">
      <template v-if="Array.isArray(footer)">
        <template v-for="(item, index) in footer">
          <component
            v-bind="item"
            :is="item.renderer"
            :key="index"
            :action="onSave"
          />
        </template>
      </template>
      <template v-else>
        <component v-bind="footer" :is="footer.renderer" :action="onSave" />
      </template>
    </div>
  </div>
</template>
<script>
export default {
  name: 'MisMonaco',
  props: {
    footer: {
      type: [Array, Object],
      required: false,
    },
  },
  data() {
    return {
      schema: {},
    };
  },
  created() {
    this.onFormatSchema();
  },
  mounted() {
    this.$eventHub.$on('mis-schema:change', this.upSchema);
    this.$eventHub.$on('mis-schema:init', this.upSchema);
    this.schema = {
      schema: 'https://github.com/songshuzhong/umis/v1/schemas/page.json',
      ...window.UMIS.schema,
    };
    this.editor = window.monaco.editor.create(this.$refs.editor, {
      fontSize: '14px',
      language: 'json',
      autoIndent: true,
      formatOnType: true,
      formatOnPaste: true,
      selectOnLineNumbers: true,
      scrollBeyondLastLine: false,
      folding: true,
      theme: 'vs-dark',
      automaticLayout: true,
      minimap: {
        enabled: false,
      },
    });

    window.editorer = this.editor;
    this.editor.setValue(JSON.stringify(this.schema));
    this.onFormatSchema();
  },
  methods: {
    upSchema(data) {
      this.schema = {
        schema: 'https://github.com/songshuzhong/umis/v1/schemas/page.json',
        ...data,
      };
      this.editor.setValue(JSON.stringify(this.schema));
    },
    onFormatSchema() {
      const timer = setTimeout(() => {
        window.editorer.getAction(['editor.action.formatDocument']).run();
        clearTimeout(timer);
      }, 100);
    },
    onSave() {
      try {
        const json = this.editor.getValue();
        this.$eventHub.$emit('mis-schema:change', JSON.parse(json));
      } catch (e) {
        console.error(e);
        this.$notice({
          type: 'error',
          title: '警告',
          message: e.toString(),
        });
      }
    },
  },
};
</script>
<style lang="scss">
.monaco-editor {
  width: 100%;
  height: calc(100vh - 110px);
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-align: left;
}

.umis-editor-container {
  width: 100%;
  margin: 0 auto;
  background-color: #282a36 !important;
}
.umis-editor-tools {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
</style>
